.card__container {
    max-width: 350px;
    max-height: 180px;
    padding: 12px;
    margin: 40px;
}
.card__text_container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 25px;
    font-size: 18px;
}
.card__thanks_text {
    text-align: center;
    font-size: 25px;
}
.card__thanks_hidden {
    display: none;
}